form {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 1%;
}

.icons {
    display: flex;
    width: 70px;
    justify-content: space-evenly;
    top: 92%;
    position: sticky;
}

.button:focus {
    border: none;
    outline: none;
}

.linkedin,
.github {
    transition: 0.5s;
}
.linkedin:hover {
    color: #c07eb1;
    transform: rotate(-360deg);
}

.github:hover {
    color: #c07eb1;
    transform: rotate(360deg);
}

.pointer {
    cursor: pointer;
}

.wrapper {
    width: 500px;
    /* padding: 20px 0; */
}

.form-group {
    position: relative;
    margin-bottom: 2.5rem !important;
    text-align: left;
    width: 100%;
}

.ph-area {
    position: absolute;
    top: 0;
    padding: 7px 0 0 13px;
    transition: 400ms;
    opacity: 0.5;
    font-family: Merienda;
}

.row {
    flex-direction: column;
}

.form-control:focus + .ph-area,
.form-control:valid + .ph-area {
    font-size: 100%;
    transform: translate3d(0, -100%, 0);
    opacity: 1;
    color: black;
}

textarea {
    resize: none;
}

.form-group:focus, textarea:focus, input:focus, label:focus {
    outline: none !important;
    outline-offset: none !important;
}

#mailSent {
    margin-top: 10%;
    text-align: center;
}

#mailSent h1 {
    border-bottom: none;
}

a:link,
a:visited {
    color: black;
}

@media screen and (max-width: 480px) {
    #mailSent {
        margin-top: 0 !important;
    }
    .contact {
        padding: 10% 15%;
    }
}

@media screen and (max-width: 320px) {
    .contact {
        padding: 20% 5% !important;
    }
}

@media screen and (min-width: 1500px) {
    .contact h1 {
        margin-top: 15px;
        padding-bottom: 15px;
    }
    .contact h4 {
        margin: 30px 0px;
    }
    .wrapper {
        width: 30%;
    }
}

@media screen and (min-width: 2500px) {
    form {
        margin-top: 5%;
    }
    .icons .fa-2x {
        font-size: 4em;
    }
}

@media screen and (min-width: 760px) and (max-width: 780px) {
    .contact-title {
        font-size: 4rem;
        margin-top: 1%;
        margin-bottom: 10%;
    }
    .contact-text {
        font-size: 2.5rem;
    }
}